<template lang="html">
  <div class="container pb-5 pt-3">
    <div class="">
        <a v-on:click="returnHome"><i class="arrow fas fa-arrow-left mr-4"></i>   </a>
        <div class="" id="top"></div>
    </div>
    <Title class="pt-5" txt="  "/>
    <div class="">
        <br />
      <btn @click.native="setIniciatorsRu" color="outline-indigo"></btn>
      <btn @click.native="setIniciatorsEn" color="outline-indigo">English</btn>
    </div>
    <div class="form-group pt-5">
      <div class="container-fluid mb-2">
        <input v-model="data_title" class="m-100 form-control col-md-3 mb-2" placeholder="" type="text" name="" value="">
        <input v-model="data_secondName" class="form-control col-md-3 mr-2 mb-3" placeholder="" type="text" name="" value="">
        <input v-model="data_firstName" class="form-control col-md-3 mr-2 mb-3" placeholder="" type="text" name="" value="">
        <input v-model="data_lastName" class="form-control col-md-3 mb-3" placeholder="" type="text" name="" value="">
      </div>
      <div class="container-fluid mb-2">
        <input v-model="data_Email" class="form-control col-md-3 mr-2 mb-3" placeholder="Email" type="text" name="" value="">
        <input v-model="data_phone" class="form-control col-md-3 mb-3" placeholder="" type="text" name="" value="">
      </div>
      <div class="row container-fluid mb-2">
        <div class="col-md-4">
          <label for="">    ?*</label>
          <select class="btn btn-outline-indigo col-md-6" v-model="selected_2">
            <option  value=""></option>
            <option>  </option>
          </select>
        </div>
        <div class="col-md-4">
          <label for="">  *</label>
          <select class="btn btn-outline-indigo" v-model="selected">
            <option disabled value="">   </option>
            <option> </option>
            <option> </option>
            <option> </option>
          </select>
        </div>
      </div>

      <div class="container-fluid mb-3">
        <div class="col-md-4 mb-4">
          <label for="#gr">   *</label>
          <input v-model="data_geographic" class="form-control col-md-10" id="gr" type="text" name="" value="">
        </div>

        <div class="col-md-4 mb-4">
          <label for="">*</label>
          <select class="btn btn-outline-indigo" v-model="selected_country">
            <option disabled value="">   </option>
            <option v-for="(item, index) of countrys">{{item.text}}</option>
          </select>
        </div>
      </div>
      <div class="col-md-4 row align-items-center container-fluid pl-2">
        <label for="#mp"> </label>
        <input v-model="checkedNames" class="form-control col-md-3" value=" " type="checkbox" name="" id="mp">
        <!-- <span>: {{ checkedNames }}</span> -->
      </div>
      <div class=" mb-2">
        <div class="col-md-4 mb-3">
          <label for="#reg"> *</label>
          <input v-model="data_region" class="form-control col-md-8 mr-2" placeholder="" type="text" id="reg" name="" value="">

        </div>
        <div class="col-md-4 mb-3">
          <label for="#gor"> *</label>
          <input v-model="data_city" class="form-control col-md-8 mr-2" placeholder="" type="text" id="rgor" name="" value="">
        </div>

        <div class="col-md-4 mb-3">
          <label for=""></label>
          <select class="btn btn-outline-indigo" v-model="selected_currency">
            <option disabled value="">   </option>
            <option v-for="(item, index) of currency">{{item.text}} ({{item.val}})</option>
          </select>
        </div>

      </div>
      <div class="container-fluid">
        <div class="col-md-4 mb-3">
          <label for="#vi"> </label>
          <select class="btn btn-outline-indigo" id="vi" v-model="selected_volumeInvest">
            <option disabled value="">   </option>
            <option v-for="(item, index) of volumeInvest">{{item.text}} </option>
          </select>
        </div>

        <div class="col-md-4">
          <label for="#ren">(%)</label>
          <input v-model="data_rentable" class="col-md-8 form-control" type="text" name="" placeholder="%" value="">
        </div>
      </div>

      <div class="container-fluid">
        <label for="#des">* (RUB)  150 )</label>
        <br />
        <textarea v-model="data_description" class="col-md-6 form-control" name="name" id="des" rows="8" cols="80"></textarea>
      </div>

      </div>
    <div class="card">
      <div class="card-header">
        <h4>  </h4>
      </div>
      <div class="card-body">
        <ul>
          <li>: {{data_title}}</li>
          <li>: {{data_secondName}}</li>
          <li>: {{data_firstName}}</li>
          <li>: {{data_lastName}}</li>
          <li>Email: {{data_Email}}</li>
          <li>: {{data_phone}}</li>
          <li> : {{data_geographic}}</li>
          <li>: {{selected_country}}</li>
          <li>: {{data_city}}</li>
          <li>: {{data_region}}</li>
          <li>: {{data_rentable}}</li>
          <li>: {{data_description}}</li>
          <li>: {{selected}}</li>
          <li>    : {{selected_2}}</li>
          <li>: {{selected_currency}}</li>
          <li> : {{selected_volumeInvest}}</li>

        </ul>
      </div>
      <div class="card-footer">
        <button type="button" v-on:click="addProposal" class="btn btn-outline-success" name="button"></button>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import Title from './ru/Title'
import { Btn, Row,Card, } from 'mdbvue'
import { Dropdown, DropdownItem, DropdownToggle,DropdownMenu } from 'mdbvue'

export default {
  name: 'SetIniciators',
  components: {
    Title,
    Btn,
    Row,
    Card,
    Dropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem,

  },
  data () {
    return {
      selected: '',
      selected_2:'',
      selected_country: '',
      selected_currency: '',
      selected_volumeInvest: '',
      picked: '',
      checkedNames: [],
      data_title: '',
      data_secondName: '',
      data_firstName: '',
      data_lastName: '',
      data_Email: '',
      data_phone: '',
      data_geographic: '',
      data_city: '',
      data_region: '',
      data_rentable: '',
      data_description: '',
      countrys: [
        { text: ''  },{ text: '' },{ text: '' },{ text: '' },{ text: '' },
        { text: '' },{ text: '' },{ text: '' },{ text: '  ' },{ text: '' },
        { text: '' },{ text: '' },{ text: ' ' },{ text: '' },{ text: '' },
        { text: '' },{ text: '' },{ text: '' },{ text: '' },{ text: '' },
        { text: '' },{ text: '' },{ text: '  ' },{ text: '' },{ text: '' },{ text: '' },
        { text: ' ' },{ text: '' },{ text: '' },{ text: '' },{ text: '' },{ text: '' },
        { text: '' },{ text: '' },{ text: ' p' },{ text: '' },{ text: '' },
        { text: '' },{ text: '' },{ text: '' },{ text: '' },{ text: '' },{ text: '' },
        { text: '-' },{ text: '' },{ text: '' },{ text: '' },{ text: '' },{ text: '' },{ text: '' },
        { text: '' },{ text: '  ' },{ text: '' },{ text: ' ' },{ text: '' },
        { text: '' },{ text: '' },{ text: '' },{ text: '' },{ text: '' },{ text: '' },{ text: '' },
        { text: '' },   { text: '' },   { text: '' }, { text: '' }, { text: '' }, { text: '' },
        { text: '' },{ text: '-' },{ text: '' },{ text: '' },{ text: '' },  { text: '' },
        { text: '' },  { text: '' },{ text: '' },{ text: '' },  { text: '' },
        { text: '' },   { text: '' },   { text: ' ' }, { text: '' },   { text: '-' },
        { text: '-' }, { text: '' }, { text: '' }, { text: '' }, { text: '' }, { text: '' }, { text: '' },
        { text: '' },{ text: '' },{ text: '' },{ text: '' },{ text: '' },
        { text: '' },{ text: '' },{ text: '' },{ text: '' }, {text:''},
        { text:''},{ text:''},{ text:''},{ text:''},{ text:''},{ text:' '},
        { text:''},{ text:''},{ text:''},{ text:''},{ text:''},{ text:''},{ text:''},
        { text:''},{ text:''},{ text:''},{ text:''},{ text:''},{ text:''},{ text:''},
        { text:' '}, { text:''}, { text:''}, { text:''}, { text:''}, { text:''}, { text:''}, { text:'- '}, { text:''}, { text:''},
        { text:''}, { text:''}, { text:' '}, { text: ' '}, { text:''},
        { text:''},{ text:''},{ text:''},{ text:''},{ text:'-'},
        { text:'-  '},{ text:' '},{ text:''},{ text:'  '},{text:''},
        { text:''},{ text:'-  '},{ text:'-  '},  { text: '-'},  { text: ''},
        { text: ''}, { text: ''}, { text: ''}, { text: ''}, { text: '  '}, { text: ' '},
        { text: ''}, { text: ''}, { text: '-'}, { text: ''}, { text: ''},
        { text: ''},{ text: ''},{ text: ''},{ text: '  '},{ text: ''},
        { text: ''},{ text: ''},{ text: ''},{ text: ''},{ text: ''},{ text: ''},
        { text: ''},{ text: ''},{ text: ''},{ text: ''},{ text: ''},
        { text: ''},{ text: ' '},{ text: ''},{ text: ''},{ text: ''},
        { text: ''},{ text: ''},{ text: ''},{ text: '-'},{ text: ''},
        { text: ' '},{ text: ''},{ text: ''},{ text: ''},{ text: '- '},
        { text: ' '},  { text: ''},  { text: ''},
      ],
      currency: [
        {text:' ', val: 'RUB'},
        {text: '', val: 'USD'},
        {text: '', val: 'EUR'},
      ],
      volumeInvest: [
        {text: ' 10 000  50 000'},
        {text: ' 50 000  250 000'},
        {text: ' 250 000  500 000'},
        {text: ' 500 000'},
      ],
    }
  },
  mounted: function () {
  setTimeout(function() {
    var t = $('#top')
    return $("body, html").animate({
        scrollTop: t
    }, 2e3), !1
  }, 1000);
  },
  methods: {
    returnHome (){
        this.$router.push({ name: 'HomeRu'})
    },
    setIniciatorsEn () {
      this.$router.push({ name: 'SetIniciatorsEn'})

    },
    setIniciatorsRu () {
      this.$router.push({ name: 'SetIniciators'})

    },
    addProposal () {
      // A post entry.
      var db = firebase.firestore();

      // Disable deprecated features
      db.settings({
        timestampsInSnapshots: true
      });

      db.collection("investors").add({
        Title:  this.data_title,
LastName:  this.data_secondName,
Name:  this.data_firstName,
MiddleName:  this.data_lastName,
Email:  this.data_Email,
Phone:  this.data_phone,
GeographicalLocation:  this.data_geographic,
Country: this.selected_country,
City:  this.data_city,
Region:  this.data_region,
ROI:  this.data_rentable,
Description:  this.data_description,
Industry:  this.selected,
AviableContact: this.selected_2,
Valtuis:  this.selected_currency,
Investment:  this.selected_volumeInvest,
        })
        .then(function(docRef) {
            console.log("Document written with ID: ", docRef.id);
        })
        .catch(function(error) {
            console.error("Error adding document: ", error);
        });

    }
  },
}
</script>

<style lang="css">
.logotip__container_set {
  height: 170px;
  padding-left: 25px;
  padding-right: 25px;
  padding-bottom: 25px;
  padding-top: 25px;
}
.dropdown__otrasli {
  padding: 0;
  margin: 0;
}
</style>



