<template lang="html">
  <div id="investorsS">
    <p class="lead  investors-text col-lg-6 col-sm-8 col-10">
       EcoStart    
      ,   
      .
        
        
      -.
    </p>
    <div class="row pl-4">
      <button
        v-on:click="setproject"
        v-bind:class="isSignUp ? 'disabled' : ''"
        type="button"
        class="btn btn-outline-indigo mb-2"
        name="button">
            
      </button>
    </div>
    <div class="row mb-5 pl-4">
      <span class="red-text" v-if="isSignUp">     </span>
    </div>

    <p class="lead ml-3 black-text"><strong></strong></p>
    <ul style="list-style-type: none;">
      <li>     <li/>
      <li> <li/>
      <li>     <li/>
      <li>   <li/>
      <li>    <li/>
      <li>   <li/>
      <li>   <li/>
      <li>  ,   EcoStart<li/>
    </ul>

  </div>
</template>

<script>

export default {
  name: 'Investors',
  data () {
    return {
      isSignUp: false,
    }
  },
  mounted () {
    if (localStorage.getItem('Logined') == 'not log') {
      console.log('not log ')
      this.isSignUp = true;
    } else if (localStorage.getItem('Logined') == null){
      console.log('null ')
      this.isSignUp = true;
    } else {
      console.log('true')
      this.isSignUp = false;

    }
  },
  methods: {
    setproject (){
        this.$router.push({ name: 'SetIniciators'})
    },
  },

}
</script>

<style lang="css">
.investors-text {
  max-width: 60%;
  margin-top: 40px;
}
#investorsS {
  padding-bottom: 60px;
}
</style>



