        scrollTop: t
    }, 200), !1
  }, 200);
  },
  methods: {
    returnHome (){
        this.$router.push({ name: 'HomeEn'})
    },
    setVolonteersEn (){
        this.$router.push({ name: 'SetVolonteersEn'})
    },
    setVolonteersRu (){
        this.$router.push({ name: 'SetVolonteers'})
    },
    addVolonteer() {
      // A post entry.
      var db = firebase.firestore();

      // Disable deprecated features
      db.settings({
        timestampsInSnapshots: true
      });

      db.collection("volonteers").add({
             LastName: this.data_secondName,
        Name: this.data_firstName,
        MiddleName: this.data_lastName,
        Country: this.selected_country,
        PlaceStudy: this.data_placeJobLearning,
        LinkNetworks: this.data_socialProfile,
        Email: this.data_email,
        })
        .then(function(docRef) {
            console.log("Document written with ID: ", docRef.id);
        })
        .catch(function(error) {
            console.error("Error adding document: ", error);
        });

    }
