<template lang="html">
  <div class="container pb-5 pt-3">
    <div class="">
        <a v-on:click="returnHome"><i class="arrow fas fa-arrow-left mr-4"></i>   </a>
        <div class="" id="top"></div>
    </div>
    <Title class="pt-5" txt=""/>
    <div class="">
       
      <btn @click.native="setVolonteersRu" color="outline-indigo"></btn>
      <btn @click.native="setVolonteersEn" color="outline-indigo">English</btn>
    </div>
     
    <ul>

    <li>1 </li>
    <li>2      </li>
    <li>3     </li>
    <li>4     </li>
    <li>5        </li>
    ,   
    <li>6      </li>
  </ul>

    <div class="form-group">
      <label for=""></label>
      <input v-model="data_secondName" type="text" class="form-control mb-3 col-md-4" name="" value="">
      <label for=""></label>
      <input v-model="data_firstName" type="text" class="form-control mb-3 col-md-4" name="" value="">
      <label for=""></label>
      <input v-model="data_lastName" type="text" class="form-control mb-3 col-md-4" name="" value="">

      <div class="">
      <label for="">*</label>
      <select class="btn btn-outline-indigo mb-3" v-model="selected_country">
        <option disabled value="">   </option>
        <option v-for="(item, index) of countrys_3">{{item.text}}</option>
      </select>
    </div>
      <label for=""> /</label>
      <input v-model="data_placeJobLearning" type="text" class="form-control mb-3 col-md-4" name="" value="">

      <label for="">    . </label>
      <input v-model="data_socialProfile" type="text" class="form-control mb-3 col-md-4" name="" value="">

      <label for="">Email</label>
