      <label for=""></label>
      <input v-model="data_password" type="text" class="form-control mb-3 col-md-4" name="" value="">

      <label for=""> </label>
      <input v-model="data_secondPassword" type="text" class="form-control mb-3 col-md-4" name="" value="">

      <div class="card mb-4">
        <div class="card-header">
          <h4>  </h4>
        </div>
        <div class="card-body">
          <ul>
            <li>: {{data_secondName}}</li>
            <li>: {{data_firstName}}</li>
            <li>: {{data_lastName}}</li>
            <li>: {{selected_country}}</li>
            <li>  /  {{data_placeJobLearning}}</li>
            <li>     : {{data_socialProfile}}</li>
            <li>Email: {{data_email}}</li>

          </ul>
</div>
      </div>


      <button type="button" v-on:click="addVolonteer" class="btn btn-outline-indigo" name="button"></button>
      <p>,   <a href="#">  .</a></p>


    </div>
  </div>
</template>

<script>
import { Btn, Row,Card} from 'mdbvue'
import Title from './ru/Title'

export default {
  name: 'SetVolonteers',
  components: {
    Title,
    Btn,
    Row,
    Card,
  },
  data () {
    return {
      selected_country: '',
      data_firstName: '',
      data_secondName: '',
      data_lastName: '',
      data_placeJobLearning: '',
      data_socialProfile: '',
      data_email: '',
      data_password: '',
      data_secondPassword: '',
      countrys_3: [
        { text: ''  },{ text: '' },{ text: '' },{ text: '' },{ text: '' },
        { text: '' },{ text: '' },{ text: '' },{ text: '  ' },{ text: '' },
        { text: '' },{ text: '' },{ text: ' ' },{ text: '' },{ text: '' },
        { text: '' },{ text: '' },{ text: '' },{ text: '' },{ text: '' },
        { text: '' },{ text: '' },{ text: '  ' },{ text: '' },{ text: '' },{ text: '' },
        { text: ' ' },{ text: '' },{ text: '' },{ text: '' },{ text: '' },{ text: '' },
        { text: '' },{ text: '' },{ text: ' p' },{ text: '' },{ text: '' },
        { text: '' },{ text: '' },{ text: '' },{ text: '' },{ text: '' },{ text: '' },
        { text: '-' },{ text: '' },{ text: '' },{ text: '' },{ text: '' },{ text: '' },{ text: '' },
        { text: '' },{ text: '  ' },{ text: '' },{ text: ' ' },{ text: '' },
        { text: '' },{ text: '' },{ text: '' },{ text: '' },{ text: '' },{ text: '' },{ text: '' },
        { text: '' },   { text: '' },   { text: '' }, { text: '' }, { text: '' }, { text: '' },
        { text: '' },{ text: '-' },{ text: '' },{ text: '' },{ text: '' },  { text: '' },
        { text: '' },  { text: '' },{ text: '' },{ text: '' },  { text: '' },
        { text: '' },   { text: '' },   { text: ' ' }, { text: '' },   { text: '-' },
        { text: '-' }, { text: '' }, { text: '' }, { text: '' }, { text: '' }, { text: '' }, { text: '' },
        { text: '' },{ text: '' },{ text: '' },{ text: '' },{ text: '' },
        { text: '' },{ text: '' },{ text: '' },{ text: '' }, {text:''},
        { text:''},{ text:''},{ text:''},{ text:''},{ text:''},{ text:' '},
        { text:''},{ text:''},{ text:''},{ text:''},{ text:''},{ text:''},{ text:''},
        { text:''},{ text:''},{ text:''},{ text:''},{ text:''},{ text:''},{ text:''},
        { text:' '}, { text:''}, { text:''}, { text:''}, { text:''}, { text:''}, { text:''}, { text:'- '}, { text:''}, { text:''},
        { text:''}, { text:''}, { text:' '}, { text: ' '}, { text:''},
        { text:''},{ text:''},{ text:''},{ text:''},{ text:'-'},
        { text:'-  '},{ text:' '},{ text:''},{ text:'  '},{text:''},
        { text:''},{ text:'-  '},{ text:'-  '},  { text: '-'},  { text: ''},
        { text: ''}, { text: ''}, { text: ''}, { text: ''}, { text: '  '}, { text: ' '},
        { text: ''}, { text: ''}, { text: '-'}, { text: ''}, { text: ''},
        { text: ''},{ text: ''},{ text: ''},{ text: '  '},{ text: ''},
        { text: ''},{ text: ''},{ text: ''},{ text: ''},{ text: ''},{ text: ''},
        { text: ''},{ text: ''},{ text: ''},{ text: ''},{ text: ''},
        { text: ''},{ text: ' '},{ text: ''},{ text: ''},{ text: ''},
        { text: ''},{ text: ''},{ text: ''},{ text: '-'},{ text: ''},
        { text: ' '},{ text: ''},{ text: ''},{ text: ''},{ text: '- '},
        { text: ' '},  { text: ''},  { text: ''},
      ],
    }
  },
  mounted: function () {
  setTimeout(function() {
    var t = $('#top')
    return $("body, html").animate({
        scrollTop: t
    }, 200), !1
  }, 200);
  },
  methods: {
    returnHome (){
        this.$router.push({ name: 'HomeRu'})
    },
    setVolonteersEn (){
        this.$router.push({ name: 'SetVolonteersEn'})
    },
    setVolonteersRu (){
        this.$router.push({ name: 'SetVolonteers'})
    },
    addVolonteer() {
      // A post entry.
      var db = firebase.firestore();

      // Disable deprecated features
      db.settings({
        timestampsInSnapshots: true
      });

      db.collection("volonteers").add({
             LastName: this.data_secondName,
        Name: this.data_firstName,
        MiddleName: this.data_lastName,
        Country: this.selected_country,
        PlaceStudy: this.data_placeJobLearning,
        LinkNetworks: this.data_socialProfile,
        Email: this.data_email,
        })
        .then(function(docRef) {
            console.log("Document written with ID: ", docRef.id);
        })
        .catch(function(error) {
            console.error("Error adding document: ", error);
        });

    }
  },
}
</script>

<style lang="css">
</style>



